:root
{
  --maincolor : #ff5e14 ;
  --maincolor : #4a2091 ;
  --maincolor : #2f135a;
  --maincolor : #16003e;
  --maincolor : #1d262d;
  --maintitlecolor : purple;
  /*--maintitlecolor : black;*/
  --notice : none ;
  --changeopacity : 0 ;
  --contentFONT : 'Palanquin', sans-serif;
}
@media (orientation: Landscape) {
  *{
    padding: 0;
    margin: 0;
  }
  .divider
  {
    height: 25px;
    transition: background-color 0.2s;
  }
  .menubar
  {
    padding: 15px;
    position: sticky;
    top: 0px;
    background: white;
    display: flex;
    justify-content: space-around;
    transition: all 0.2s;
    z-index: 1;
  }
  .rightmenu
  {
    display: flex;
    align-items: center;
    gap: 12px;
  }
  .search
  {
    border: 1.5px solid black;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    padding: 0px 14px; /* This Padding is dependend on searchbtn height*/
    border-right-color: transparent;
    outline: none;
    font-family: 'Didact Gothic', sans-serif;
  }
  .searchbar
  {
    display: flex;
  }
  .searchbtn
  {
    background-color: white;
    background-image: url("images/search.png");
    background-repeat: no-repeat;
    background-size: 15px;
    background-position: center;
    height: 32px;
    width: 28px;
    border-right: 1px solid black;
    border-top: 1.5px solid black;
    border-bottom: 1.5px solid black;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    border-left-color: transparent;
  }
  .Logo
  {
    font-family: 'Orbitron', sans-serif;
    font-size: 20px;
    transform: translate(-0px,-3px);
    transition: color 0.1s;
    user-select: none;
  }
  .leftmenu
  {
    width: 200px;
    display: flex;
    justify-content: space-around;
    transform: translate(0px, 5px);
  }
  .icon {
    height: 20px;
    width: 20px;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .notification
  {
    height: 22px;
    width: 22px;
  }
  .iconholder:before
  {
    content: "";
    position: absolute;
    height: 6px;
    width: 6px;
    border-radius: 50%;
    transform: translate(12px);
    background: red;
    display: var(--notice);
  }
  .content
  {
    padding-top: 100px;
  }
  .written
  {
    display: grid;

    grid-template-columns: 1.5fr 1fr;
  }
  .vid
  {
    height: 490px;
    width: 490px;
    outline: none;
    clip-path: inset(1px 1px);
  }
  .aboutwritten
  {
    display: grid;
    place-items: start;

  }
  .animetionabout
  {
    background: white;
    align-self: center;
    justify-self: center;
    display: grid;
  }
  .NavigationSection
  {
    /*width: 100px;*/
    /*height: 100px;*/
    display: flex;
    gap: 50px;
    align-items: start;
  }
  .writtenleft
  {
    width: 500px;
  }
  .classicbtn
  {
    padding:10px 30px;
    background: transparent;
    border: 2px solid black;
    border-radius: 20px;

  }
  .arrow
  {
    height: 12px;
    transform: rotate(-45deg);
  }
  .Projects
  {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  .card
  {
    margin: 20px;
    height: 450px;
    width: 250px;
    background: white;
    border-radius: 20px;
    box-shadow:0px 0px 10px #e0e0e0;
    opacity: var(--changeopacity);
    transition: opacity 1.5s;
  }
  .cardupper img
  {
    height: 100px;
  }
  .cardupper
  {
    /*background: red;*/
    height: 350px;
    display: grid;
    place-items: center;
  }
  .cardlower
  {
    /*background: red;*/
    height: 100px;
    display: grid;
    place-items: center;
    font-size: 18px;
  }
}
a{
  text-decoration: none;
}
