/*  drop down menu for potrait*/
  .dropdown {
    position: relative;
    display: inline-block;
  }

  .dropdown-content {
    height: 00px;
    position: absolute;
    background-color: white;
    width: 111vw;
    /*box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);*/
    z-index: 1;
    top: 55px;
    /*left: -14px;*/
    transition: height 0.2s, background 0.2s;
    overflow: hidden;
  }
  .dropdown-content_toggle
  {
    display: grid;
    grid-template-columns:1fr 1fr 1fr ;
    place-items: center;
    height: 50px;
  }
  .unmovable-dropdown-content
  {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;
