.modalback
{
    position: absolute;
    height: 100vh;
    width: 100vw;
    background: rgba(128, 128, 128, 0);
    background: rgba(0, 0, 0, 0.4);
    z-index: 2;
    top: 0;
    left: 0;
    transition: background 0.5s, opacity 0.5s;
    display: grid;
    place-items: center;
    position: fixed;
    opacity: 0;
    z-index: -10;
}
@media (orientation: Landscape) {
    .modalbox
    {
        height: 300px;
        width: 550px;
        background: white;
        border-radius: 20px;
        display: grid;
        grid-template-rows: 0.2fr 0.6fr 0.2fr;
    }
}
@media (orientation: Portrait) {
    .modalbox
    {
        height: 200px;
        width: 350px;
        background: white;
        border-radius: 20px;
        display: grid;
        grid-template-rows: 0.2fr 0.6fr 0.2fr;
    }
}
.modalbox h2
{
    font-family: 'Kanit', sans-serif;
    justify-self: center;
}
.modalbox p
{
    font-family: 'Kanit', sans-serif;
    padding: 20px;
}
.modalbox div
{
    display: flex;
    flex-direction: row-reverse;
}
.modalbox div button
{
    padding: 10px 30px;
    border-radius: 20px;
    border: 0px;
    border-top-right-radius: 0;
    border-bottom-left-radius: 0;
    background: white;
    color: #4a2091;
    font-weight: bold;
    letter-spacing: 1px;
    border-top: 2px solid white;
    border-left: 2px solid white;
    transition: color 0.2s;
}
.modalbox div button:hover
{
    color: rgba(102, 51, 153, 0.8) ;
}
@keyframes noticein {
    from
    {
        opacity: 0;
        z-index: 2;
    }
    to
    {
        opacity: 1;
        z-index: 2;
    }
}
@keyframes noticeout {
    to
    {
        opacity: 0;
        z-index: -1;
    }
    from
    {
        opacity: 1;
        z-index: 50;
    }
}
.show
{
    animation: noticein 0.5s forwards;
}
.hide
{
    animation: noticeout 0.5s forwards;
}