*{
    padding: 0;
    margin: 0;
}

a{
    text-decoration: none;
}

@media (orientation: Landscape) {
    .blogsarea
    {
        display: grid;
        place-items: center;
    }
    .Blogs
    {
        background: transparent;
        width: 80vw;
        padding-top: 50px;
    }
    .Blog
    {
        padding: 20px 50px;
        background: rgba(245, 245, 245, 0.32);
        font-family: 'Palanquin', sans-serif;
        border-radius: 10px;
        margin: 60px 0px;
    }
    .Blog h2
    {
        letter-spacing: 1px;
        margin-bottom: 10px;
    }
    .Blog p{
        letter-spacing: 1px;
    }
    .BlogButtonCheckOutSection
    {
        /*background: red;*/
        /*padding: 10px;*/
        display: flex;
        justify-content: end;
    }
    .CheckBlog
    {
        padding: 10px 20px;
        background: transparent;
        color: var(--maintitlecolor);
        border: none;
        border-radius: 10px;
        transition: background 0.4s;
        letter-spacing: 1px;
    }
    .CheckBlog:hover
    {
        background: #ececec;
    }
    .IndexTogglearea
    {
        /*background: green;*/
        height: 50px;
        display: flex;
        align-items: center;
        padding-left: 20px;
        background: #fcfcfc;

        /*border-bottom: 2px solid lightgray;*/
    }
    .IndexMenuicon
    {
        height: 25px;

    }
    .BlogReader
    {
        /*background: green;*/
        height: 80vh;
        /*width: 200px;*/
        /*margin: 50px;*/
        display: grid;
        grid-template-columns: auto auto;
        overflow: hidden;
    }
    .BlogIndex
    {
        background: #fcfcfc;
        /*position: absolute;*/
        height: 80vh;
        width:300px;
        transition: width 0.2s;
        overflow: hidden;
    }
    .BlogIndexToggle
    {
        width: 0px;
    }
    .index
    {
        padding:20px;
        width: 300px;
    }
    .BLogtitles
    {
        background: transparent;
        padding: 15px;
        font-size: 14px;
        color: var(--maintitlecolor);
        transition: background 0.2s, color 0.2s;
        font-family: var(--contentFONT);
        letter-spacing: 0.5px;
        border-radius: 10px;
        user-select: none;
    }
    .BLogtitles:hover
    {
        background: rgb(117, 125, 148);
        color: white;
    }
    .BlogContent
    {
        /*background: blue;*/
        padding: 100px;
        letter-spacing: 1px;
        overflow: scroll;
        scroll-behavior: smooth;

    }
    /* blog content elements*/
    .blogtitlehead
    {
        font-family: var(--contentFONT);
        padding: 5px;
    }
    .blogparra
    {
        font-family: var(--contentFONT);
        letter-spacing: 0.5px;
        padding: 5px;
    }
}
